import React, { Component } from "react";
import axios from "axios";
import {
	Card,
	Table,
	Tag,
	Tooltip,
	message,
	Button,
	Modal,
	Layout,
	Typography,
	Radio,
	Input,
	Row,
	Col,
	Image,
	Space,
} from "antd";
import {
	EyeOutlined,
	DeleteOutlined,
	CheckOutlined,
	CloseOutlined,
} from "@ant-design/icons";
import {
	CompressOutlined,
	UserOutlined,
	CalendarOutlined,
} from "@ant-design/icons";
import dayjs from "dayjs";
import AvatarStatus from "components/shared-components/AvatarStatus";
import userData from "assets/data/user-list.data.json";

const { Header, Footer, Sider, Content } = Layout;

const { TextArea } = Input;

export class Identity extends Component {
	state = {
		users: [],
		userProfileVisible: false,
		selectedUser: null,
		isModalOpen: false,
		radioValue: "",
		rejectReasonValue: "",
	};

	componentDidMount() {
		axios.get("http://localhost:5000/api/management/allUsers").then((res) => {
			this.setState({
				users: res.data.allUser,
			});
		});
	}

	handleStatus = (user, newStatus) => {
		this.setState((prevState) => ({
			users: prevState.users.map((item) => {
				if (item._id === user._id) {
					return {
						...item,
						IDStatus: newStatus,
						IDStatusComment: this.state.rejectReasonValue,
					};
				}
				return item;
			}),
		}));
	};

	handleRejectReason = (user, newRejectReason) => {
		this.setState((prevState) => ({
			users: prevState.users.map((item) => {
				if (item.id === user.id) {
					return { ...item, rejectReason: newRejectReason };
				}
				return item;
			}),
		}));
	};

	showModal = (user) => {
		console.log(user.status);
		this.setState({
			isModalOpen: true,
			selectedUser: user,
			radioValue: user.IDStatus,
			rejectReasonValue: user.IDStatusComment,
		});
	};
	handleOk = () => {
		this.setState({
			isModalOpen: false,
		});
		axios
			.post("http://localhost:5000/api/management/changeIDStatus", {
				...this.state.selectedUser,
				IDStatus: this.state.radioValue,
				IDStatusComment: this.state.rejectReasonValue,
			})
			.then((res) => {
				this.handleStatus(this.state.selectedUser, this.state.radioValue);
				console.log(res.data);
			})
			.catch((err) => {
				console.log(err);
			});
	};
	handleCancel = () => {
		this.setState({ isModalOpen: false });
	};

	handleStatusChange = (index, newStatus) => {
		const updatedLists = [...this.state.lists];
		updatedLists[index] = newStatus;
		this.setState({ lists: updatedLists });
		console.log(this.state.lists);
	};

	onChangeRadio = (e) => {
		console.log("radio checked", e.target.value);
		this.setState({
			radioValue: e.target.value,
		});
		// this.setState((prevState) => ({
		//   users: prevState.users.map((item) => {
		//     if (item.id === this.state.selectedUser.id) {
		//       return { ...item, status: this.state.radioValue };
		//     }
		//     return item;
		//   }),
		// }));
		this.handleStatus(this.state.selectedUser, this.state.radioValue);
	};

	onChangeRejectReason = (e) => {
		this.setState({
			rejectReasonValue: e.target.value,
		});
		this.handleRejectReason(
			this.state.selectedUser,
			this.state.rejectReasonValue
		);
	};

	render() {
		const { users, userProfileVisible, selectedUser } = this.state;
		const defaultAvatar = "/img/avatars/default.png";

		const tableColumns = [
			{
				title: "User",
				dataIndex: "name",
				render: (_, record) => (
					<div className="d-flex">
						<AvatarStatus
							src={record.avatar !== "" ? record.avatar : defaultAvatar}
							name={record.firstname + " " + record.lastname}
							subTitle={record.email}
						/>
					</div>
				),
				sorter: {
					compare: (a, b) => {
						a = a.firstname.toLowerCase();
						b = b.firstname.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0;
					},
				},
			},
			{
				title: "Last online",
				dataIndex: "lastOnline",
				render: (date) => <span>{dayjs.unix(date).format("MM/DD/YYYY")} </span>,
				sorter: (a, b) =>
					dayjs(a.lastOnline).unix() - dayjs(b.lastOnline).unix(),
			},
			{
				title: "Status",
				dataIndex: "status",
				render: (_, elm) =>
					elm.IDStatus === false ? (
						<Tooltip title={elm.IDStatusComment}>
							<Tag className="text-capitalize" color={"red"}>
								rejected
							</Tag>
						</Tooltip>
					) : (
						<Tag
							className="text-capitalize"
							color={elm.IDStatus === true ? "cyan" : "orange"}
						>
							{elm.IDStatus === true ? "approved" : "pending"}
						</Tag>
					),
				sorter: {
					compare: (a, b) => a.IDStatus.length - b.IDStatus.length,
				},
			},
			{
				title: "Actions",
				dataIndex: "actions",
				render: (_, elm) => (
					<div className="text-right d-flex justify-content-end">
						<Tooltip title="View">
							<Button
								type="primary"
								className="mr-2"
								icon={<EyeOutlined />}
								onClick={() => this.showModal(elm)}
								size="small"
							/>
						</Tooltip>
					</div>
				),
			},
		];
		return (
			<Card bodyStyle={{ padding: "0px" }}>
				<div className="table-responsive">
					<Table columns={tableColumns} dataSource={users} rowKey="id" />
				</div>
				<Modal
					open={this.state.isModalOpen}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					// okButtonProps={{ disabled: true }}
					okButtonProps={{
						disabled:
							this.state.radioValue === false &&
							this.state.rejectReasonValue === "",
					}}
					centered
					width="40%"
				>
					<Layout>
						<Header style={{ padding: "0 20px" }}>
							<p style={{ fontSize: "28px", marginBottom: "0px" }}>
								Identity Information
							</p>
						</Header>
						<Content
							style={{
								padding: "0 50px",
								background: "none",
							}}
						>
							<Typography style={{ marginBottom: "10px" }}>
								<Image src={this.state.selectedUser?.IDCardUrl} />
							</Typography>
							<Typography style={{ margin: "20px" }}>
								<Radio.Group
									onChange={this.onChangeRadio}
									value={this.state.radioValue}
								>
									<Radio value={true}>Approve</Radio>
									<Radio value={false}>Reject</Radio>
								</Radio.Group>
							</Typography>
							{this.state.radioValue === false ? (
								<TextArea
									value={this.state.rejectReasonValue}
									onChange={this.onChangeRejectReason}
									placeholder="Please enter the reject reason."
									autoSize={{ minRows: 3, maxRows: 5 }}
								/>
							) : null}
						</Content>
					</Layout>
				</Modal>
			</Card>
		);
	}
}

export default Identity;
